import moment from 'moment';
import React, { FC, useEffect } from 'react';
import { Calendar, momentLocalizer, Views } from 'react-big-calendar';
import { useDispatch } from 'react-redux';
import { Col, Row } from 'reactstrap';
import { add, currentDate, PageTitle, WrapperSimple } from 'summer';
import { setCurrentState } from '../../../../store/ducks/global.duck';

const allViews = Object.keys(Views).map(k => Views[k]);
const localizer = momentLocalizer(moment);
const current = currentDate();
const events = [
  {
    allDay: true,
    end: add(current, 2, 'day'),
    id: 1,
    start: add(current, 1, 'day'),
    title: 'Envio do eSocial',
  },
  {
    end: add(current, 5, 'days'),
    id: 2,
    start: add(current, 2, 'days'),
    title: 'Envio do SPED ICMS',
  },
];
const defaultMessages = {
  agenda: 'Agenda',
  allDay: 'Dia todo',
  date: 'Data',
  day: 'Dia',
  event: 'Evento',
  month: 'Mês',
  next: 'Próximo',
  noEventsInRange: 'Não existem eventos nessas datas.',
  previous: 'Voltar',
  showMore: total => `+ ${total} mais`,
  time: 'Hora',
  today: 'Hoje',
  tomorrow: 'Amanhã',
  week: 'Semana',
  work_week: 'Semana de Trabalho',
  yesterday: 'Ontem',
};

const Dashboard: FC = () => {
  const dispatch = useDispatch();

  useEffect(
    () => {
      dispatch(setCurrentState('GER'));
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <>
      <PageTitle pageTitleShadow={true} titleHeading="Módulo Principal" />
      <Row>
        <Col>
          <WrapperSimple sectionHeading="Agenda">
            <Calendar
              localizer={localizer}
              views={allViews}
              step={60}
              showMultiDayTimes={true}
              events={events}
              startAccessor="start"
              endAccessor="end"
              messages={defaultMessages}
              style={{ minHeight: 650 }}
            />
          </WrapperSimple>
        </Col>
      </Row>
    </>
  );
};

export default Dashboard;
